body {
  background: $white;
  line-height: 1.4;
  font-size: $fz;
  min-width: 320px;
  @include reg;
  color: $c-text;
  @include no-scroll;
  @include sm {
    font-size: $fz-sm-px; } }

.out {
  @include lg {
    padding-top: $header-height-lg; } }

.icon {
  display: inline-block;
  fill: currentColor;
  font-size: inherit; }

.lazy {
  &.is-loaded:not(.no-fade-in) {
    animation: fadeIn 1s; } }

.container {
  width: 100%;
  padding-left: 15px;
  padding-right: 15px;
  margin-left: auto;
  margin-right: auto;
  max-width: 1440px; }

.logo {
  font-size: 0;
  display: inline-block;
  @include xs {
    width: 80px; }
  @include xss {
    width: 60px; } }

.close {
  @include icon-close(20, 1, currentColor);
  position: absolute;
  z-index: 1;
  top: 20px;
  right: 20px;
  @include tr(color .3s);
  @include hover {
    color: $accent; } }

