.hero {
  $this: &;
  position: relative;
  background-color: $gray-lightest;
  &__bg {
    position: absolute;
    z-index: 0;
    top: 0;
    bottom: 0;
    width: 50%;
    @include cover;
    @include lg {
      // background-position: 0 50%
      @include before {
        @include coverdiv;
        z-index: 0;
        background-color: rgba($white, .7); } }
    @include md {
      background-position: 60% 50%; }
    @include sm {
      background-position: 70% 50%;
 } }      // display: none
  &__bg-left {
    left: 0; }
  &__bg-right {
    right: 0;
    @include xll {
      background-position: 0 50%; } }
  &__inner {
    position: relative;
    z-index: 1; }
  &__content {
    max-width: 295px;
    @include sm {
      max-width: none; } }
  &__right {
    padding-left: 50px;
    @include lg {
      padding-left: 0; } }
  &__label {
    position: absolute;
    z-index: 2;
    top: 25px;
    left: 50%;
    transform: translate(-50%, 0);
    width: 160px;
    @include lg {
      top: 15px; } }

  &--home {
    #{$this}__inner {
      @include lg {
        padding-top: 50px; }
      @include sm {
        padding-top: 150px;
        padding-bottom: 30px; } }
    #{$this}__left, #{$this}__right {
      padding-top: 25%;
      padding-bottom: 25%;
      @include sm {
        padding-top: 0;
        padding-bottom: 0; } }
    #{$this}__left {
      @include sm {
        margin-bottom: 30px; } } } }

.hero-label {
  color: $accent;
  text-align: center;
  &__icon {
    width: 55px;
    height: 55px;
    border-radius: 50%;
    background-color: $white;
    @include flex(center, center);
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 10px;
    overflow: hidden;
    position: relative;
    .icon {
      font-size: em(25);
      position: relative;
      z-index: 1; }
    @include before {
      background-color: #e8e8e8;
      position: absolute;
      right: 0;
      top: 0;
      bottom: 0;
      width: 50%;
      z-index: 0; } }


  &__title {
    font-weight: 700;
    font-size: $fz-sm; } }
