.section {
  padding-top: 60px;
  padding-bottom: 60px;
  position: relative;
  @include xl {
    padding-top: 50px;
    padding-bottom: 50px; }
  @include lg {
    padding-top: 40px;
    padding-bottom: 40px; }
  @include md {
    padding-top: 30px;
    padding-bottom: 30px; }
  &__title {
    margin-bottom: 30px;
    @include md {
      margin-bottom: 15px; } }
  &__bg {
    background-color: $gray;
    @include coverdiv;
    z-index: 0;
    iframe {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%; } }
  &__inner {
    position: relative;
    z-index: 1; }

  &--gray {
    background-color: $gray-lightest; } }

.s-contacts {
  @include xs {
    padding-top: 0; }
  .section__bg {
    @include xs {
      position: relative;
      width: 100%;
      top: auto;
      left: auto;
      right: auto;
      bottom: auto;
      padding-top: 70%;
      margin-bottom: 15px; } } }

