.nav {
  color: $white;
  font-weight: 300;
  ul {
    @include flex(space-between, center);
    @include lg {
      display: block; } }
  li {
    @include lg {
      padding-top: 8px;
      padding-bottom: 8px; } }
  a {
    @include tr(color .3s);
    @include hover {
      color: #a9a9a9;
      text-decoration: underline; }
    &.is-active {
      color: #a9a9a9;
      text-decoration: underline; } }

  &--footer {
    color: $c-title;
    font-size: $fz-sm;

    ul {
      display: block; }
    li {
      @include lg {
        padding-top: 0;
        padding-bottom: 0; }
      @include notlast {
        margin-bottom: 10px; } }
    a {
      @include hover {
        text-decoration: none;
        color: $c-text; }
      &.is-active {
        text-decoration: none;
        color: $c-text; } } } }
